import good from'../../components/goods/good.vue'
import uniDrawer from '../../components/uni-drawer/uni-drawer.vue'
export default{
	data(){
		return{
			isShow:false,
			visible:false,
			dataList:[], //搜索结果列表
			q:"",        //搜索关键词
			nodata:false,//暂无数据标识
			page_no:1,  //当前也是
			page_size:10, //每页数据个数
		}
	},
	components:{
		good,
		uniDrawer
	},
	onLoad() {
		this.getSearchList();
		//事件监听搜索结果
		// uni.$on('getlist',(data) => {
		// 	console.log(data.getlist)
			
		// })
		//this.dataList = this.$store.state.searchList;
		//console.log(this.$store.state.searchList)
	},
	computed:{
		// dataList(){
		//  return	this.$store.getters.searchList;
		// }
	},
	methods:{
		//搜索列表
		getSearchList(){
			const self = this;
			uni.getStorage({
			    key: 'search_key',
			    success: function (res) {
			        //console.log(res.data);
							self.q = res.data;
			    }
			});
			const data = {q:this.q,page_no:1,page_size:100};
			this.requestData(data)
		},
		requestData(data){
			const self = this;
			uni.request({
				url:self.$API.URL + self.$API.tbk_super_search,
				data:data,
				success(res){
					//console.log(res)
					if( res.data.msg.code != 200){
						self.nodata = true;
					}else{
						self.nodata = false;
						self.page_no = self.page_no + 1;
						res.data.msg.data.map(item => {
							self.dataList.push(item);
						})
						
					}
					
				}
			})
		},
		//分页加载
		loadMore(event){
			//console.log(event.detail)
			const data = {q:this.q,page_no:this.page_no,page_size:this.page_size};
			this.requestData(data)
		},
		//搜索
		goSearch(){
			this.$CommonJS.navigateTo('./search');
		},
		//筛选
		filterType(){
			this.visible = true
		},
		//重置
		closeMask(){
			this.visible = false;
		},
		goback:function(){
			this.$CommonJS.navigateBack();
		}
	}
}
